
body, html {
  height: 100%;
  font-family: Helvetica Neue, Helvetica, Hiragino Sans GB, Microsoft YaHei, \\5FAE\8F6F\96C5\9ED1, Arial, sans-serif;
  margin: 0;
}
body {
  background: url(http://ordlnvdfx.bkt.clouddn.com/bg.jpg) no-repeat 50%;
  background-size: cover;
}
.wx-app {
  max-width: 1000px;
  min-width: 800px;
  height: 80%;
  min-height: 600px;
  padding-top: 100px;
  margin: 0 auto;
  -webkit-transition: .2s;
  transition: .2s;
}
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-thumb {
  border-radius: 3px;
  -moz-border-radius: 3px;
  -webkit-border-radius: 3px;
  background-color: #6f6f6f;
}
::-webkit-scrollbar-track {
  background-color: transparent;
}
@media (max-height: 800px), (max-width: 1000px) {
.wx-app {
    padding-top: 0;
    height: 100%;
}
}

.wx-aside {
  float: left;
  position: relative;
  width: 280px;
  height: 100%;
  background-color: #2e3238;
}

.wx-aside-header {
  color: #ffffff;
  border-bottom: 1px solid #24272c;
}

.wx-profile {
  padding: 18px;
}
.wx-profile__avatar {
    display: inline-block;
    vertical-align: middle;
    width: 40px;
    height: 40px;
    cursor: pointer;
}
.wx-profile__username {
    display: inline-block;
    vertical-align: middle;
    width: 160px;
    margin-left: 5px;
    color: #ffffff;
}
.wx-profile__menu {
    display: inline-block;
    vertical-align: middle;
    width: 30px;
    line-height: 30px;
    right: 0;
    text-align: center;
    font-size: 20px;
    color: #777777;
    cursor: pointer;
}

.wx-search {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  line-height: 32px;
  width: 100%;
  padding: 0 18px;
}
.wx-search__icon {
    position: absolute;
    left: 24px;
    top: 1.3px;
    font-size: 19px;
    bottom: 0;
    color: #808285;
}
.wx-search__input {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    width: 100%;
    line-height: 32px;
    padding-left: 30px;
    border: 0;
    border-radius: 2px;
    outline: none;
    color: #ffffff;
    background: #26292e;
}
.wx-search-container {
  position: absolute;
  z-index: 99999;
  top: 41px;
  left: 18px;
  right: 18px;
  background: #2e3238;
  -webkit-box-shadow: 0 0 10px #2a2a2a;
          box-shadow: 0 0 10px #2a2a2a;
  border-radius: 2px;
}
.wx-search-container__et {
    padding: 0px 9px;
    font-weight: 400;
    color: #787b87;
    background: #393c43;
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px;
}
.wx-search-result {
  max-height: 420px;
  overflow: auto;
}
.wx-search-result__tips {
    padding: 0px 9px;
    font-size: 14px;
    color: #787b87;
    background: #393c43;
}
.wx-search-result-item {
  line-height: 30px;
  padding: 10px 9px;
  cursor: pointer;
  border-bottom: 1px solid #33363b;
  background: #393c43;
}
.wx-search-result-item.is-first {
    background: #595b64;
}
.wx-search-result-item__avatar {
    display: block;
    float: left;
    width: 30px;
    height: 30px;
    margin-right: 15px;
    border-radius: 2px;
}
.wx-search-result-item__username {
    overflow: hidden;
    text-overflow: ellipsis;
}

.wx-wx-aside-tab {
  overflow: hidden;
}
.wx-aside-tab-container {
  display: inline-block;
  width: 33%;
  padding: 8px 0;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  cursor: pointer;
}
.wx-aside-tab-container__icons {
    width: 100%;
    display: block;
    font-size: 22px;
}
.wx-aside-tab-container__icons.is-active {
      color: #3caf36;
}
.wx-aside-tab-container + .wx-aside-tab-container > .wx-aside-tab-container__icons {
  border-left: 1px solid #24272c;
}

.wx-chat-list {
  position: absolute;
  top: 149px;
  bottom: 0px;
  left: 0;
  right: 0;
  overflow: auto;
  overflow-y: auto;
}
.wx-chat-list__tips {
    margin: 30px auto;
    text-align: center;
    color: #6b6f7c;
    font-size: 14px;
}
.wx-chat-list__item {
    padding: 18px;
    height: 40px;
    overflow: hidden;
    cursor: pointer;
}
.wx-chat-list__item:hover {
      background: #3a3f45;
}
.wx-chat-list__item:hover * {
        color: #ffffff;
}
.wx-chat-list__item.is-active {
      background: #3a3f45;
}
.wx-chat-list__item.is-active * {
        color: #ffffff;
}
.wx-chat-list__avatar {
    width: 40px;
    height: 40px;
    margin-right: 10px;
    float: left;
}
.wx-chat-list-info {
  color: #989898;
  font-size: 13px;
  overflow: hidden;
}
.wx-chat-list-info span {
    display: block;
    line-height: 20px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.wx-chat-list-info__username {
    color: #ffffff;
}
.wx-chat-list-info__time {
    float: right;
    color: #6b6f7c;
}
.wx-chat-list-info__lastMsg {
    font-size: 13px;
    color: #989898;
}

.wx-subject-list {
  position: absolute;
  top: 149px;
  bottom: 0px;
  left: 0;
  right: 0;
  overflow: auto;
  overflow-y: auto;
}
.wx-subject-list__et {
    margin: 30px auto;
    text-align: center;
    color: #6b6f7c;
    font-size: 14px;
}

.wx-contact-list {
  position: absolute;
  top: 149px;
  bottom: 0px;
  left: 0;
  right: 0;
  overflow: auto;
  overflow-y: auto;
}
.wx-contact-list__tips {
    margin: 30px auto;
    text-align: center;
    color: #6b6f7c;
    font-size: 14px;
}
.wx-contact-list__group-title {
    margin: 0;
    padding: 5px 18px;
    font-weight: 400;
    color: #787b87;
    background: #292d32;
    font-size: 14px;
}
.wx-contact-list__item {
    padding: 10px 18px 9px;
    color: #ffffff;
    cursor: pointer;
}
.wx-contact-list__item + .wx-contact-list__item {
      border-top: 1px solid #292c33;
}
.wx-contact-list__item.is-active {
      background: #3b4047;
}
.wx-contact-list__item__avatar {
    width: 30px;
    height: 30px;
    float: left;
    margin-right: 7px;
}
.wx-contact-list__item__name {
    line-height: 30px;
    font-size: 13px;
    overflow: hidden;
}

.wx-main {
  position: relative;
  height: 100%;
  overflow: hidden;
  background: #eeeeee;
}

.wx-chatArea-content {
  position: absolute;
  top: 52px;
  left: 0;
  right: 0;
  bottom: 190px;
  padding: 0 20px;
  overflow: auto;
}
.wx-chatArea-content__time {
    font-size: 12px;
    padding: 1px 18px;
    color: #b2b2b2;
    text-align: center;
}
.wx-chatArea-message {
  text-align: left;
  margin-top: 15px;
  margin-bottom: 15px;
}
.wx-chatArea-message__bubble {
    position: relative;
    display: inline-block;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    max-width: 500px;
    min-height: 40px;
    white-space: pre-line;
    word-wrap: break-word;
    background-color: #ffffff;
    line-height: 30px;
    padding: 5px 10px;
    text-align: left;
    font-size: 14px;
    border-radius: 3px;
}
.wx-chatArea-message__bubble::after {
      content: " ";
      position: absolute;
      top: 14px;
      right: 100%;
      border: 6px solid transparent;
      border-right-color: #ffffff;
      border-right-width: 4px;
}
.wx-chatArea-message__avatar {
    width: 40px;
    height: 40px;
    float: left;
    margin-right: 15px;
    margin-left: 0;
}
.wx-chatArea-message.is-self {
    text-align: right;
}
.wx-chatArea-message.is-self > img {
      float: right;
      margin-right: 0;
      margin-left: 15px;
}
.wx-chatArea-message.is-self > div {
      background-color: #b2e281;
}
.wx-chatArea-message.is-self > div::after {
        content: " ";
        position: absolute;
        top: 14px;
        right: -10px;
        border: 6px solid transparent;
        border-left-color: #b2e281;
        border-left-width: 4px;
}
.wx-chatArea-messageArea {
  min-height: 190px;
  position: absolute;
  margin-right: 19px;
  border-top: 1px solid #d6d6d6;
  bottom: 0;
  left: 0;
  right: 0;
}
.wx-chatArea-messageArea__bar {
    line-height: 30px;
    padding: 5px 17px;
}
.wx-chatArea-messageArea__icon {
    font-size: 18px;
}
.wx-chatArea-messageArea-text-box {
  position: absolute;
  width: 100%;
  left: 0;
  right: 0;
  top: 42px;
  bottom: 40px;
}
.wx-chatArea-messageArea-text-box__wrapper {
    height: 100%;
    padding-left: 20px;
}
.wx-chatArea-messageArea-text-box__wrapper > textarea {
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      border: 0;
      font-size: 16px;
      font-family: inherit;
      background: none;
      outline: none;
      overflow: auto;
      resize: none;
}
.wx-chatArea-messageArea-btn-container {
  line-height: 30px;
  position: absolute;
  bottom: 10px;
  text-align: right;
  left: 0;
  right: 0;
}
.wx-chatArea-messageArea-btn-container__describe {
    font-size: 12px;
    margin-left: 10px;
    margin-right: 7px;
    color: #888888;
}
.wx-chatArea-messageArea-btn-container__send {
    padding: 3px 30px;
    display: inline-block;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    color: #222;
    background: #ffffff;
    border: 1px solid #c1c1c1;
}
.wx-chatArea-tip-wrapper {
  margin-top: 120px;
  text-align: center;
}
.wx-chatArea-tip-wrapper__icon {
    color: #e2e2e2;
    font-size: 100px;
}
.wx-chatArea-tip-wrapper__tip {
    color: #cccccc;
    margin: 0;
    margin-top: 5px;
    font-size: 13px;
}

.wx-main-title {
  line-height: 30px;
  text-align: center;
  padding: 10px 0;
  margin: 0 19px;
  border-bottom: 1px solid #d6d6d6;
}
.wx-main-title > span {
    display: inline-block;
    vertical-align: middle;
    max-width: 300px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    word-wrap: normal;
    text-decoration: none;
    color: #000000;
    font-weight: 400;
    font-size: 14px;
}




















.wx-contactArea {
  padding: 80px 0;
  text-align: center;
}
.wx-contactArea__avatar {
    width: 100px;
    height: 100px;
    margin-bottom: 22px;
    border-radius: 4px;
}
.wx-contactArea__username {
    display: inline-block;
    margin: 0;
    font-weight: 400;
    font-size: 24px;
}
.wx-contactArea__icon {
    font-size: 12px;
}
.wx-contactArea__icon.is-male {
      color: #5da7ca;
}
.wx-contactArea__icon.is-female {
      color: #dd6665;
}
.wx-contactArea__bio {
    margin: 0;
    margin-top: 10px;
    font-size: 14px;
    color: #888888;
}
.wx-contactArea__another-info {
    margin: 35px;
    color: #888888;
}
.wx-contactArea__another-info > p {
      margin: 0;
      line-height: 1.6;
      font-size: 12px;
}
.wx-contactArea__another-info > p > label {
        white-space: pre-wrap;
}
.wx-contactArea__newChat-btn {
    display: inline-block;
    width: 200px;
    line-height: 40px;
    font-size: 14px;
    border: 0;
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    text-align: center;
    color: #ffffff;
    background: #42ac3e;
}
.wx-contactArea-tip-wrapper {
  text-align: center;
  margin-top: 50px;
}
.wx-contactArea-tip-wrapper__icon {
    display: inline-block;
    color: #f0f0f0;
    padding: 20px;
    font-size: 100px;
    background: #e0e0e0;
}

.wx-copyright {
  margin: 0;
  left: 0;
  right: 0;
  position: absolute;
  bottom: 30px;
  text-align: center;
  font-size: 12px;
  color: #e3e3e3;
}
@media (max-height: 800px) {
.wx-copyright {
    display: none;
}
}

.wx-menu {
  display: inline-block;
  position: absolute;
  min-width: 125px;
  padding: 0;
  margin: 0;
  list-style: none;
  border-radius: 4px;
  border: 1px solid #d6d6d6;
  background: #ffffff;
  -webkit-box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.1);
          box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.1);
}

.wx-menu-item {
  padding: 8px;
  line-height: 1.6;
  font-size: 14px;
  cursor: pointer;
  border-bottom: 1px solid #f1f1f1;
}
.wx-menu-item:hover {
    background: #f5f5f5;
}
.clearfix:after {
  content: "";
  clear: both;
  display: block; }
